import React, { Suspense, lazy } from 'react';
import ReactDOM from 'react-dom';
import { ThemeProvider } from '@mui/material/styles';
import { HashRouter, Routes, Route } from 'react-router-dom';
import Stack from '@mui/material/Stack';
import CircularProgress from '@mui/material/CircularProgress';
import Typography from '@mui/material/Typography';

import './index.css';
//  import App from './App';
//  import CreateMemory from './pages/memory/Create';
//  import Memories from './pages/memories/List';
import PrelaunchTheme from './theme';
//  import ViewMemory from './pages/memory/View';
import { ScrollToTop } from './components/common';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import Authenticate from './hocs/require-authentication';

import './fonts/BadScript-Regular.ttf';

const bucket = process.env.REACT_APP_S3_BUCKET;
const authURL = process.env.REACT_APP_AUTH_URL;

const LazyApp = lazy(() => import('./App'));
const LazyCreateMemory = lazy(() => import('./pages/memory/create/CreateMemory'));
const LazyViewMemory = lazy(() => import('./pages/memory/view/ViewMemory'));
const LazyViewSharedMemory = lazy(() => import('./pages/memory/view/ViewSharedMemory'));
const LazyMemories = lazy(() => import('./pages/memories/Memories'));

//  const MemoriesRoute = Authenticate(LazyMemories);
//  const CreateMemoryRoute = Authenticate(LazyCreateMemory);
//  const ViewMemoryRoute = Authenticate(LazyViewMemory);
//  const ViewSharedMemoryRoute = Authenticate(LazyViewSharedMemory);

const loading = (
  <Stack
    direction="row"
    justifyContent="space-between"
    alignItems="center"
    spacing={2}
    sx={{
      position: 'absolute', top: '50vh', right: '50vw', transform: 'translate(50%, -50%)', backgroundColor: 'form.input', boxShadow: 10, padding: 2,
    }}
  >
    <CircularProgress />
    <Typography variant="h6">Loading ...</Typography>
  </Stack>
);

ReactDOM.render(
  <React.StrictMode>
    <ThemeProvider theme={PrelaunchTheme}>
      <HashRouter basename={process.env.PUBLIC_URL}>
        <ScrollToTop />
        <Routes>
          <Route
            exact
            path="/"
            element={(
              <Suspense fallback={loading}>
                <Authenticate>
                  <LazyMemories />
                </Authenticate>
              </Suspense>
            )}
          />
          <Route
            path="/login"
            element={(
              <Suspense fallback={loading}>
                <LazyApp authURL={authURL} />
              </Suspense>
            )}
          />
          <Route
            exact
            path="/memories/:memoryMode?"
            element={(
              <Suspense fallback={loading}>
                <Authenticate>
                  <LazyMemories initMode="own" />
                </Authenticate>
              </Suspense>
            )}
          />
          <Route
            path="/memory/create"
            element={(
              <Suspense fallback={loading}>
                <Authenticate>
                  <LazyCreateMemory bucket={bucket} authURL={authURL} />
                </Authenticate>
              </Suspense>
            )}
          />
          <Route
            path="/memory/:memoryId"
            element={(
              <Suspense fallback={loading}>
                <Authenticate>
                  <LazyViewMemory bucket={bucket} authURL={authURL} />
                </Authenticate>
              </Suspense>
            )}
          />
          <Route
            path="/memory/shared/:memoryId"
            element={(
              <Suspense fallback={loading}>
                <Authenticate>
                  <LazyViewSharedMemory />
                </Authenticate>
              </Suspense>
            )}
          />
          <Route
            path="*"
            element={(
              <Suspense fallback={<div>Loading...</div>}>
                <LazyApp authURL={authURL} />
              </Suspense>
            )}
          />
        </Routes>
      </HashRouter>
    </ThemeProvider>
  </React.StrictMode>,
  document.getElementById('root'),
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorkerRegistration.register();
